.works {
    display: grid;
    position: relative;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
    margin-bottom: 150px; 
      

    &__left {
    position: absolute;
    width: 440px;
    height: 384px;
}
    &__right {
    position: absolute;
    width: 671px;
    height: 269px;
}
}